export let getHistorySongs = () => {
  return JSON.parse(localStorage.getItem('h-songs') || '[]')
}

export let setHistorySongs = (val) => {
  return localStorage.setItem('h-songs', JSON.stringify(val))
}

export let getLikeSongs = () => {
  return JSON.parse(localStorage.getItem('l-songs') || '[]')
}

export let setLikeSongs = (val) => {
  return localStorage.setItem('l-songs', JSON.stringify(val))
}


