import { Dialog } from 'vant'
import { getmid } from '@/utils/song'
import { setHistorySongs } from '@/utils/storage'

const state = {
  listShow: false,
  playerShow: false,
  isPlay: false,
  songList: [],
  playerList: [],
  currentIndex: -1,
  loading: true,
  mid: '',
  firstError: '',
  historySongs: [],
  player: new QMplayer({
    onplay() {
      state.loading = false
    },
    onended() {
      state.currentIndex++
    },
  }),
}
const mutations = {
  showListState(state) {
    state.listShow = !state.listShow
  },
  playerShowState(state, val=true) {
    state.playerShow = val
  },
  songList(state, list) {
    state.songList = getmid(list)
  },
  addSongList(state, val) {
    if (state.songList.some(e => e.mid === val.mid)) return
    state.songList.unshift(val)
    state.playerList.unshift(val)
  },
  clearSongList(state) {
    state.songList = []
  },
  playerList(state, list) {
    state.playerList = list
  },
  liked(state, idx) {
    let one = state.playerList[idx]
    one.liked = !one.liked
    state.playerList.splice(idx, 1, one)
  },
  currentIndex(state, val) {
    let { length } = state.playerList
    if (val < 0) {
      state.currentIndex = length - 1
    } else if (val >= length) {
      state.currentIndex = 0
    } else {
      state.currentIndex = val
    }
  },
  syncList(state) {
    state.playerList = state.songList
  },
  toggle(state) {
    state.isPlay = !state.isPlay
    state.player.toggle()
  },
  mid(state, val) {
    state.mid = val
  },
  setCurrentTime(state, val) {
    state.currentTime = val
  },
  historySongs(state, val) {
    state.historySongs = val
  },
  addHistorySongs(state, val) {
    if (!state.historySongs.some(e => e.mid === val.mid)) {
      state.historySongs.push(val)
    }
  },
  removeHistorySongs(state, idx) {
    state.historySongs.splice(idx, 1)

    // 本地存储
    setHistorySongs(state.historySongs)
  },
}
const getters = {
  getInfo(state) {
    return state.playerList[state.currentIndex]
  },

}
const actions = {
  removeSongList({ state, dispatch }, idx) {
    if (state.currentIndex === idx) {
      state.player.pause()
      state.songList.splice(idx, 1)
      dispatch('play', idx)
    } else {
      state.songList.splice(idx, 1)
    }
  },
  playNext({ dispatch, state, commit }) {
    commit('currentIndex', state.currentIndex + 1)
    dispatch('start')
  },
  playPrev({ dispatch, state, commit }) {
    commit('currentIndex', state.currentIndex - 1)
    dispatch('start')
  },
  play({ state, commit, dispatch }, idx=0) {
    let { mid } = state.playerList[idx]
    if (state.mid === mid) return
    commit('currentIndex', idx)
    commit('playerShowState', true)
    dispatch('start')
  },
  start({ state, commit, getters, dispatch }) {
    let { error, mid } = getters.getInfo
    if (state.firstError === mid) {
      Dialog.alert({
        message: '当前列表全部不能播放，是否清空？',
      }).then(_=> {
        playerList = []
      })
      return
    }
    if (error) {
      dispatch('playNext')
      return
    }
    commit('mid', mid)
    state.player.play(state.mid)
    state.loading = true
    state.isPlay = true
    let timer = setTimeout(_=> {
      if (state.loading) {
        Dialog.alert({
          message: '加载失败！',
        }).then(_=> {
          if (!state.firstError) {
            state.firstError = mid
          }
          state.playerList[state.currentIndex].error = true
          dispatch('playNext')
        })
      }
      commit('addHistorySongs', getters.getInfo)

      // 本地存储
      setHistorySongs(state.historySongs)

      clearTimeout(timer)
    }, 5000)
  },
}
export default {
  namespaced: true,
  actions,
  state,
  getters,
  mutations,
}


